export default {
	data() {
		return {
			index: '',
			date:'',//日期
			day1:[],//16日
			day2:[],//17日
			day3:[],//18日
		}
	},
	
	onLoad() {
		this.recordInit();
		this.dayInit();
	},
	
	methods: {
		//读取静态数据json.js
		async recordInit() {
			let record = await this.$api.lyfwcwd('record');
			this.record = record.data;
		},
		
		async dayInit() {
			let record = await this.$api.lyfwcwd('record');
			var recordDay = record.data;
			//第一天
			let dayIndex1 = recordDay.filter(item =>{
				return item.day == 1;
			})
			this.day1 = dayIndex1;
			// console.log(dayIndex1)
			//第二天
			let dayIndex2 = recordDay.filter(item =>{
				return item.day == 2;
			})
			this.day2 = dayIndex2;
			//第三天
			let dayIndex3 = recordDay.filter(item =>{
				return item.day == 3;
			})
			this.day3 = dayIndex3;
		},
		
		//景点内容点击
		godetail: function(value) {
			uni.showToast({
				title: '你点击了' + value,
				icon: 'none'
			})
			// setTimeout(function() {
			// 	uni.navigateTo({
			// 		url: 'touristroute'
			// 	})
			// }, 500);
		},
		
		// 统一跳转接口
		navTo(url) {
			uni.navigateTo({
				url
			})
		},
	}
}
